
	export default {
		onLaunch: function() {
			console.log('App Launch')
      




      
      let that = this
      // 获取设备状态栏高度
      
      uni.getSystemInfo({
        success: function (res) {
          console.log(res)
          that.$store.commit('setHeight', res)
          // .statusBarHeight
        }
      });
      
      
      let userinfo = uni.getStorageSync('userinfo')
      if(userinfo) {
        if(userinfo.token) {
          that.$store.commit('setToken', userinfo.token)
          if(userinfo.mobile) {
            uni.switchTab({
              url: './pages/index/index'
            })
            return
          } else {
            uni.navigateTo({
              url: './pages/login/bindTel'
            })
          }
        } else {
          uni.redirectTo({
            url: './pages/login/login'
          })  
        }
      } else {
        uni.redirectTo({
          url: './pages/login/login'
        })
      }
      
